*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
  font-size: 62.5%;
}

body {
  font-family: "Montserrat", sans-serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
  overflow-x: hidden;
}

.header {
  width: 100vw;
  height: 100vh;
  background-image: url("https://images.unsplash.com/photo-1712246437466-fedea86a99f5?q=80&w=1974&auto=format&fit=crop&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D");
  background-repeat: no-repeat;
  background-size: cover;
  display: grid;
  place-items: center;
}

.header-text {
  font-size: 10rem;
  text-transform: uppercase;
}

.section-content {
  width: 100vw;
  height: 100vh;
  background-color: #1b1b1b;
  color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.section-content h1 {
  font-size: 7rem;
  margin-bottom: 5vh;
}

.section-text {
  width: 80%;
  font-size: 4rem;
  line-height: 58px;
}

.section-content > * {
  color: hsla(0, 0%, 100%, 0.2);
  background-image: linear-gradient(90deg, red, blue);
  background-size: 0% 100%;
  background-clip: text;
  background-repeat: no-repeat;
  animation: scroll-reveal linear forwards;
  animation-timeline: view();
}

.section-content h1 {
  animation-range-start: cover 20vh;
  animation-range-end: cover 50vh;
}

.section-content p {
  animation-range-start: cover 50vh;
  animation-range-end: cover 80vh;
}

@keyframes scroll-reveal {
  to {
    background-size: 100% 100%;
  }
}